/* You can add global styles to this file, and also import other style files */

@font-face{
    font-family: 'Poppins Medium';
    src: url(assets/css/Fonts/Poppins/Poppins-Medium.ttf);
}
@font-face {
    font-family: 'Poppins Light';
    src: url(assets/css/Fonts/Poppins/Poppins-Light.ttf);
}
@font-face {
    font-family: 'Poppins Thin';
    src: url(assets/css/Fonts/Poppins/Poppins-Thin.ttf);
}
:root{
    --border-radius-active-menu: 10px;
    --font-family-Poppins-L: 'Poppins Light';
    --font-family-Poppins-M: 'Poppins Medium';
    --font-family-Poppins-T: 'Poppins Thin';
    --circle-header-container-date: 8px;
    --circle-reply-comment: 6px;
}
*{
    margin: 0;
    padding: 0;
    font-family: var(--font-family-Poppins-M);
    box-sizing: border-box;
}